<template>
  <div class="container">
    <h1>App coming soon...</h1>
    <span class="car">ðŸš—{{exhaust}}</span>
  </div>
</template>

<script>
export default {
  data () {
    return {
      exhaust: ""
    }
  },
  mounted() {
    setInterval(() => {
      console.log("in interval");
      if (this.exhaust === "...") {
        this.exhaust = ""
      } else {
        this.exhaust = this.exhaust + "."
      }

      console.log("exhaust should be " , this.exhaust);
    }, 250)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  h1 {
    text-align: center;
    color: white;
  }

  .car {
    position: absolute;
    left: 0%;
    font-size: 50px;
    transform: scaleX(-1);
    animation: zoom 4s infinite linear;
    color: #fff;

    width: 200px;
    margin-left: -200px;
  }
  @keyframes zoom {
    0% {
      left: 0%
    }
    100% {
      left: 100%
    }
  }
</style>



